import { useState } from "react";
import { useParams } from "react-router-dom";
import { Nav } from 'react-bootstrap'
import { useEffect } from "react";
// import data from './data';
import { addItem } from './store.js'
import { useDispatch } from 'react-redux'


function Detail(props) {

  // let [shoes, setShoes] = useState(data);


    let { id } = useParams();



  //  console.log(props.shoes[id]);
  //  console.log(props.shoes[id].id);
  //  console.log(props.shoes[id].title);

  //  let copy3 = [...shoes].sort((a, b) =>
  //  a.title > b.title ? 1 : -1,
  //   );
  //  console.log(copy3);

  //  console.log(copy3[id].title);
    





    // console.log(Number(id) + 1);

 
    // let selproduct = props.shoes.find(function(x){
    //   return x.id == id
    // });

   

    let selproduct  = props.shoes.find((x) => x.id == id )

    let dispatch = useDispatch()

    

    let [tap, setTap] = useState(0);

    let [fade2, setFade2] = useState('')

    useEffect(()=>{
      setFade2('end')
      return ()=>{
        setFade2('')
      }
    },[])



    return (

        <div className={'container start ' + fade2}>
         

            <div className="row">
                <div className="col-md-6">

                    <img src={'/'+selproduct.imgUrl} width="80%" />

                </div>
                <div className="col-md-6">
                    <h4 className="pt-5">{selproduct.title}</h4>
                    <p>{selproduct.content}</p>
                    <p>{selproduct.price}</p>
                    <button className="btn btn-danger" onClick={()=>{
                      //  dispatch(addItem(  {id : 2, name : 'Grey Yordan', count : 1}))

                    

                      dispatch(addItem({
                        id : selproduct.id, 
                        name : selproduct.title,
                        count : 1,
                      }))



                    }}></button>
                </div>
            </div>






            <Nav variant="tabs"  defaultActiveKey="link0">
                <Nav.Item>
                 <Nav.Link  onClick={()=>{ setTap(0) }} eventKey="link0">0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                 <Nav.Link onClick={()=>{ setTap(1) }} eventKey="link1">1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                 <Nav.Link  onClick={()=>{ setTap(2) }} eventKey="link2">2</Nav.Link>
                </Nav.Item>
            </Nav>

            {/* <div>0</div>
            <div>1</div>
            <div>2</div>  */}

            <TabContent tap={tap}/>

        </div>
    )
}



function TabContent({tap}){

    let [fade, setFade] = useState('')
  
     useEffect( ()=>{
      setTimeout(()=>{ setFade('end')},100)
      return ()=>{
          setFade('')
      }

     } ,[tap])
  
    return (
      <div className={'start ' + fade}>
        { [<div>0</div>, <div>1</div>, <div>2</div>][tap] }
      </div>
    )
  }


export default Detail;