import './App.css';

import { Button, Navbar, Container, Nav } from 'react-bootstrap'
import bg from './img/bg.jpg'
import { useState } from 'react';
import data from './data';
import nike from './nike';
import { Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom'
import Detail from './Detail';
import axios from 'axios'
import Cart from './Cart.js'
// import { getCurrentScope } from 'immer/dist/internal';





function App() {

  let [shoes, setShoes] = useState(data);

  let [nike10, setNike10] = useState(nike);

  let [count, setCount] = useState(1);


  let [res1, setRes1] = useState([0,1,2,3,4,5,6,7,8]);




  // console.log(shoes[0].price);

  // let [sortdata, setSortdata] = useState(shoes);


  let navigate = useNavigate();

  return (
    <div className="App">

      <>
        <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand href="#home" onClick={() => { navigate('/') }}></Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
              <Nav.Link onClick={() => { navigate('/detail/0') }}>Detail</Nav.Link>
              <Nav.Link onClick={() => { navigate('/Cart') }}>Cart</Nav.Link>
            </Nav>
          </Container>
        </Navbar>


   

        {/* 
        <Link to="/"></Link>
        <Link to="/detail"></Link> */}

        <Routes>
          <Route path="/" element={

            <>
              <div className="bg" style={{ backgroundImage: 'url(' + bg + ')' }}></div>

              {/* <div className="slider"></div> */}

              <Title/>

              <Button variant="outline-primary"  onClick={()=>{

                let copy3 = [...shoes].sort((a, b) =>
                  a.title > b.title ? 1 : -1,
                );
                // console.log(copy3);
                
                setShoes(copy3);
             
                shoes = copy3;
        

                var res=[];

                for(let i in shoes) {
                  res.push(shoes[i].id);
                }
         
                setRes1(res1=res);

       

              }}> </Button>{' '}






              <Button variant="outline-secondary" onClick={()=>{

                  let copy4 = [...shoes].sort((a, b) =>
                    a.price > b.price ? 1 : -1,
                  );
                  console.log(copy4);
                  setShoes(copy4);


                  shoes = copy4;
        

                  var res=[];
  
                  for(let i in shoes) {
                    res.push(shoes[i].id);
                  }
           
                  setRes1(res1=res);
  


              }}> </Button>{' '}




              <Button variant="outline-secondary" onClick={()=>{

              let copy5 = [...shoes].sort((a, b) =>
                b.price > a.price ? 1 : -1,
              );
              console.log(copy5);
              setShoes(copy5);
            
              console.log(shoes = copy5) 
       

              var res=[];

              for(let i in shoes) {
                res.push(shoes[i].id);
              }
              // console.log(res);
              setRes1(res1=res);
              // console.log("111");
              // console.log(res1);
                

              }}> </Button>{' '}


         


              <div className="container">


                <div className="row" >
                  {
                    shoes.map((ele, i) => {
                      return (
                        // <Product shoes={shoes[i]} i={i + 1} j={i} className="proText" />

                        <Product shoes={shoes[i]} res1={res1} i={i}  className="proText" />
                      )
                    })
                  }
                </div>

        

              </div>



                <Title2/>


 
                <Button variant="outline-success" count = {count} onClick={() => {

                   

                    if(count==1){
                      axios.get('https://dino-21.github.io/react_data/nike2.json').then((result)=>{
                          let copy10 =[...nike10, ...result.data];
                          setNike10(copy10);
                          setCount(2);

                 
                    })}else if(count==2){
                      axios.get('https://dino-21.github.io/react_data/nike3.json').then((result)=>{
                        let copy11 =[...nike10, ...result.data];
                        setNike10(copy11);
                        setCount(3);
                       })   
                    }
            
            
                    if(count===3){
                      alert("  .");  
                    }
                  

                }}> + 3    </Button>{' '}                
                

                <div className="container" style={{marginTop:'30px'}}>
                    <div className="row">
                      {
                        nike10.map((ele, i) => {
                          return (
                            <Nike nike10={nike10[i]} className="proText" />
                          
                          )
                        })
                      }
                    </div>
                </div>  

         


         
            </>

          } />



          
    

          <Route path="/detail/:id" element={<Detail shoes={shoes} />} />

          <Route path="/cart" element={<Cart/>}/>

          {/* 404 */}
          {/* <Route path="*" element={<div> .</div>} /> */}


          <Route path="/about" element={<About />} >
            <Route path="member" element={<div></div>} />
            <Route path="location" element={<div></div>} />
          </Route>


        </Routes>




 


        <Footer/>

      </>

    </div>
  );
}


function Product(props) {
  // console.log(props.res1[0]);
  // console.log(props.res1);
  return (

    <div className="col-md-4">
 
      {/* <a href={"/detail/" + props.i} >   */}
      <a href={"/detail/" + props.res1[props.i]} >  
        <img src={props.shoes.imgUrl} width="80%" />
        <h5>{props.shoes.title}</h5>
        <span>{props.shoes.content}</span>
        <p>{props.shoes.price}</p>
      </a>
    </div>

  );

}



function Nike(props) {


  return (

    <div className="col-md-4">

        <img src={props.nike10.imgUrl} width="80%" />
        <h5>{props.nike10.title}</h5>
        <span>{props.nike10.content}</span>
        <p>{props.nike10.price}</p>
      
    </div>

  );

}




function About() {
  return (
    <>
      <h4></h4>
      <Outlet></Outlet>
    </>
  );

}

const Title =()=>{
  let csst1 = {
    marginTop:"70px",
  }
  return(
    <>
      <h3 style={csst1}>MD's Pick  </h3>
      <p>   ,  .</p>
    </>
  );

}


const Title2 =()=>{
  let csst1 = {
    marginTop:"70px",
  }
  return(
    <>
      <h3 style={csst1}>  </h3>
      <p>,       .</p>
    </>
  );

}



const Footer = ()=>{
  let foo ={
    color:"#fff",
    backgroundColor:"#000",
    padding:"20px 0px",
    marginTop:"80px"
  }
  return(
    <>
      <p style={foo}>COPYRIGHT(C) 2022 Nike, Inc. All Rights Reserved</p>
    </>

  );
}

export default App;